"use client";

import { useState } from "react";
import { useTodos } from "@/hooks/useTodos";
import { Trash2, CheckCircle, Circle, Plus } from "lucide-react";
import clsx from "clsx";

export default function TodoPage() {
  const { todos, loading, addTodo, toggleTodo, deleteTodo } = useTodos();
  const [newTodo, setNewTodo] = useState("");

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!newTodo.trim()) return;
    await addTodo(newTodo);
    setNewTodo("");
  };

  if (loading) {
    return <div className="p-8">Loading tasks...</div>;
  }

  return (
    <div className="max-w-4xl">
      <h1 className="mb-8 text-3xl font-bold text-gray-800">My Tasks</h1>

      <form onSubmit={handleSubmit} className="mb-8 flex gap-4">
        <input
          type="text"
          value={newTodo}
          onChange={(e) => setNewTodo(e.target.value)}
          placeholder="Add a new task..."
          className="flex-1 rounded-lg border border-gray-300 px-4 py-3 focus:border-blue-500 focus:outline-none"
        />
        <button
          type="submit"
          className="flex items-center rounded-lg bg-blue-600 px-6 py-3 font-semibold text-white hover:bg-blue-700"
        >
          <Plus className="mr-2 h-5 w-5" />
          Add Task
        </button>
      </form>

      <div className="space-y-3">
        {todos.map((todo) => (
          <div
            key={todo.id}
            className={clsx(
              "flex items-center justify-between rounded-lg bg-white p-4 shadow-sm transition-all hover:shadow-md",
              todo.completed && "bg-gray-50"
            )}
          >
            <div className="flex items-center gap-4">
              <button
                onClick={() => toggleTodo(todo.id, !todo.completed)}
                className={clsx(
                  "text-gray-400 hover:text-blue-500",
                  todo.completed && "text-green-500 hover:text-green-600"
                )}
              >
                {todo.completed ? (
                  <CheckCircle className="h-6 w-6" />
                ) : (
                  <Circle className="h-6 w-6" />
                )}
              </button>
              <span
                className={clsx(
                  "text-lg",
                  todo.completed && "text-gray-400 line-through"
                )}
              >
                {todo.text}
              </span>
            </div>
            <button
              onClick={() => deleteTodo(todo.id)}
              className="text-gray-400 hover:text-red-500"
            >
              <Trash2 className="h-5 w-5" />
            </button>
          </div>
        ))}
        {todos.length === 0 && (
          <p className="text-center text-gray-500">
            No tasks yet. Add one above!
          </p>
        )}
      </div>
    </div>
  );
}
